const validasiPassword = (value) => {
  const errors = {};
  if (!value.new_password) {
    errors.new_password = "Tidak Boleh kosong";
  } else if (value.new_password.length < 6) {
    errors.new_password = "Password kurang dari 6";
  }
  if (!value.retype_new_password) {
    errors.retype_new_password = "Tidak Boleh kosong";
  } else if (value.retype_new_password.length < 6) {
    errors.retype_new_password = "Password kurang dari 6";
  } else if (value.retype_new_password !== value.new_password) {
    errors.retype_new_password = "Password tidak sama";
  }
  return errors;
};

export default validasiPassword;
