import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { renderField } from "./renderField";
import validasiPassword from "./Validations/validasiPassword";

class EditPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isShow: false,
    };
  }
  render() {
    return (
      <div>
        <form>
          <div className="col-lg-12">
            <Field
              component={renderField}
              label="Password Lama"
              name="password"
              type={this.state.isShow ? "text" : "password"}
            />
          </div>
          <div className="col-lg-12">
            <Field
              component={renderField}
              label="Password Baru"
              name="new_password"
              type={this.state.isShow ? "text" : "password"}
            />
          </div>
          <div className="col-lg-12">
            <Field
              component={renderField}
              label="Retype Password Baru"
              name="retype_new_password"
              type={this.state.isShow ? "text" : "password"}
            />
          </div>
          <div className="col-lg-12">
            <div className="form-control-transparant">
              <label>
                <Field
                  name="StatusTitipan"
                  component="input"
                  type="checkbox"
                  value="Open"
                  className="mr-2"
                  onClick={() =>
                    this.setState({
                      isShow: !this.state.isShow,
                    })
                  }
                />
                Show Password
              </label>
            </div>
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button
                className="btn btn-primary"
                onClick={this.props.handleSubmit}
              >
                <i className="fa fa-paper-plane mr-3"></i> Simpan
              </button>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

EditPassword = reduxForm({
  form: "EditPassword",
  enableReinitialize: true,
  validate: validasiPassword,
})(EditPassword);
export default connect()(EditPassword);
