import React, { Component } from "react";
import HeaderCard from "./HeaderCard";
import Avatar from "react-avatar";
import EditPassword from "./EditPassword";
import { AxiosMasterPut } from "./Axios";
import { berhasil, gagal } from "./Alert";
import { reset } from "redux-form";
import { connect } from "react-redux";

const maptostate = (state) => {
  return {
    username: state.provinsi.username,
  };
};
class EditProfile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isShow: false,
    };
  }

  handleChangePassword(hasil) {
    let data = {
      password: hasil.password,
      new_password: hasil.new_password,
      retype_new_password: hasil.retype_new_password,
    };
    AxiosMasterPut("users/change-password", data)
      .then(() => berhasil("Berhasil Mengganti Password"))
      .then(() => this.props.dispatch(reset("EditPassword")))
      .catch(() =>
        gagal("Sepertinya ada kesalahan, silahkan ulangi beberapa saat lagi")
      );
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-lg-4">
            <div className="card">
              <HeaderCard title="Profile" />
              <div className="card-body">
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-lg-3 col-md-6">
                      <Avatar
                        color={"#5F76E8"}
                        name={this.props.username}
                        round
                        size="60"
                      />
                    </div>
                    <div className="col-lg-8 col-md-6">
                      <h3 style={{ fontWeight: 600 }}>{this.props.username}</h3>
                      <h5>OWNER</h5>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-8">
            <div className="card">
              <HeaderCard title="Edit Password" />
              <div className="card-body">
                <EditPassword
                  onSubmit={(data) => this.handleChangePassword(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(EditProfile);
